
import { Options, Vue } from 'vue-class-component';

enum flashType {
  success, error
}

interface Flash {
  message: string;
  type: flashType;
}

@Options({
  data: () => ({
    flashMessages: [] as Flash[],
  }),

  methods: {
    newFlash(data: Flash) {
      this.flashMessages.push(data);
    },
  },
})

export default class Home extends Vue {}
